<template>
    <div class="box">
       <navbar></navbar>
        <div class="content">
            <div class="f-box warp-1200 ov">
                <div class="fl left">
                    <p class="title">一、自动滑落制度</p>
                    <div class="sub ov">
                        <img class="fl" src="@/static/images/star.png" width="12" height="12" alt="">
                        <p class="fl">假设您已经推了A、B和C，那么您的下级三人已满</p>
                        <img class="fl" src="@/static/images/star.png" width="12" height="12" alt="">
                        <p class="fl">然后您再直推第四个人D入局时，D将从左到右、从上到下滑落到1/2/3/4/5/6/7/8/9的位置，哪个位置空缺就滑落到哪个，扶持了您的下级。</p>
                    </div>
                </div>
                <img class="fl" src="@/static/images/bg04.png" width="279" height="339" alt="">
            </div>
            <div class="t-box warp-1200 ov">
                <div class="fl">
                    <img src="@/static/images/chatu.png" width="372" height="294" alt="">
                </div>
                <div class="fr right">
                    <p class="title">二、无限直推制度</p>
                    <div class="sub ov">
                        <img class="fl" src="@/static/images/star.png" width="12" height="12" alt="">
                        <div class="fl">
                            <p>接上面的例子：假设D挂在了A的1位置，而D升级一级的钱是交给您的（不会交给A），也就是说，虽然是一推三制度，但是理论上每个人有无限的直推奖，您可以推无数人的直推，虽然这些直推是挂在您的下级下面，但而这些直推奖都是直接交给您的，没有交给您的下级。</p>
                        </div>
                    </div>
                </div>
            </div>
            <div class="f-box warp-1200 ov footer">
                <div class="fl left" style="margin-top: 120px;">
                    <p class="title">三、间接扶持下级</p>
                    <div class="sub ov">
                        <img class="fl" src="@/static/images/star.png" width="12" height="12" alt="">
                        <div class="fl">
                            <p>接上面的例子：</p>
                            <p>当自动滑落在A下面的用户超过2层时，A就可以收到下2级用户升级2级的资金。</p>
                            <p>当自动滑落在A下面的用户超过3层时，A就可以收到下3级用户升级3级的资金。</p>
                            <p>当自动滑落在A下面的用户超过4层时，A就可以收到下4级用户升级4级的资金。</p>
                            <p>以此类推</p>
                        </div>
                    </div>
                </div>
                <img class="fl" src="@/static/images/bg05.png" width="333" height="577" alt="">
            </div>

            <div class="f-box warp-1200 ov footer" style="padding-bottom: 0;padding-top: 0;">
                <div class="fl left" style="width: 1112px;">
                    <p class="title">四、等级不够将流失收益</p>
                    <div class="fl">
                        <div class="sub ov">
                            <img class="fl" src="@/static/images/star.png" width="12" height="12" alt="">
                            <div class="fl">
                                <p>1、自己的等级是几级，就可以收到下几级的升级资金。</p>
                                <p>举例来说：</p>
                                <p>您的等级是1级，您可以收到下1级的升级资金。</p>
                                <p>您的等级是2级，您可以收到下2级的升级资金。</p>
                                <p>以此类推</p>
                            </div>
                        </div>
                        <img src="@/static/images/imge031.png" width="339" height="417" alt="">
                        <div class="sub ov" style="margin-top: 104px;">
                            <img class="fl" src="@/static/images/star.png" width="12" height="12" alt="">
                            <div class="fl">
                                <p style="color: #0076FF;">所以您需要尽快升级到高级别，这样下级缴纳的升级资金才不会流失</p>
                            </div>
                        </div>
                    </div>


                    <div class="fr">
                        <div class="sub ov">
                            <img class="fl" src="@/static/images/star.png" width="12" height="12" alt="">
                            <div class="fl">
                                <p>2、下级的等级超过了自己，那么下级的升级费用您就收不到了</p>
                                <p>举例来说：</p>
                                <p>您的等级是2级，您就收不到下级升级3级的钱（及3级以上的钱）</p>
                                <p>您的等级是3级，您就收不到下级升级4级（及4级以上的钱）</p>
                                <p>例如：</p>
                                <p>您的等级为1级，您推荐了A和B，收到了A和B升级1级的资金，而您没有及时升级2级，A和B推荐的伙伴升级为2级（及升2级以上的钱）的资金您就收不到了，这部分收益将流失。</p>
                            </div>
                        </div>
                        <img src="@/static/images/imge041.png" width="339" height="417" alt="">
                    </div>
                </div>
            </div>

            <div class="f-box warp-1200 ov footer">
                <div class="fl left" style="width: 620px;margin-top: 0;">
                    <p class="title">升级公式</p>
                    <div class="sub ov">
                        <img class="fl" src="@/static/images/star.png" width="12" height="12" alt="">
                        <div class="fl">
                            <p>请及时关注您的下级情况，保持您的等级高于下级层数，否则会错失下级收益</p>
                        </div>
                    </div>
                    <div class="sub ov">
                        <img class="fl" src="@/static/images/star.png" width="12" height="12" alt="">
                        <div class="fl">
                            <p style="color: #0076FF;">升级公式：</p>
                            <p style="color: #0076FF;">您应该升级的等级=您的下级层数+2</p>
                            <p>例如：</p>
                            <p>假设您下级是1层，建议您的级别至少在3级（您的下级层数1+2=3），最好在3级以上，这样才不会错失下级升级收益。</p>
                            <p>假设您下级是2层，建议您的级别至少在4级（您的下级层数2+2=4），最好在4级以上，这样才不会错失下级升级收益。</p>
                            <p>以此类推</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { Navbar } from '@/components'
    export default {
        name: "Rule" ,
        components: {
            Navbar
        },
    }
</script>

<style scoped>
     @import "../../static/css/rule.css";
</style>



